import { collection, addDoc, getDocs, getDoc, updateDoc, deleteDoc, doc, serverTimestamp, query, orderBy, where, writeBatch } from 'firebase/firestore';
import { db } from '../firebase';

const PO_COLLECTION = 'purchaseOrders';
const ACTIVITY_COLLECTION = 'poActivity';

export function normalizePOForSave(raw) {
    const items = Array.isArray(raw.items) ? raw.items : [];
    // Normalize operations: accept either `operations` array or single `operation` string
    const normalizedOperations = Array.isArray(raw.operations)
        ? raw.operations.filter(Boolean)
        : (raw.operation ? [String(raw.operation)] : []);

    const safeItems = items.map((it) => {
        const quantity = Number(it.quantity) || 0;
        const unitPrice = Number(it.unitPrice ?? it.rate) || 0;
        return {
            description: String(it.description || '').trim(),
            quantity,
            unitPrice,
            amount: Number(it.amount ?? (quantity * unitPrice)).toFixed ? Number((quantity * unitPrice).toFixed(2)) : (quantity * unitPrice),
            unit: String(it.unit || 'pcs'),
            specifications: String(it.specifications || ''),
            deliveryDate: it.deliveryDate || '',
            itemId: it.itemId || null,
            // Per-item operation (optional)
            operation: it.operation || null,
        };
    });

    const subtotal = safeItems.reduce((sum, it) => sum + (Number(it.amount) || 0), 0);
    const discountAmount = Number(raw.discountAmount || 0);
    const afterDiscount = subtotal - discountAmount;
    const taxRate = Number(raw.taxRate || 0);
    const taxAmount = (afterDiscount * taxRate) / 100;
    let finalAmount = afterDiscount + taxAmount;

    // Handle optional round-off like Delivery Challan form
    const roundOffEnabled = Boolean(raw.roundOffEnabled);
    let roundOffValue = Number(raw.roundOffValue || 0);
    if (roundOffEnabled) {
        const rounded = Math.round(finalAmount);
        roundOffValue = Number((rounded - finalAmount).toFixed(2));
        finalAmount = rounded;
    }

    // Initialize per-operation status maps
    const operationsStatus = normalizedOperations.reduce((acc, op) => {
        acc[String(op)] = (raw.operationsStatus && raw.operationsStatus[op]) || 'pending';
        return acc;
    }, {});
    const operationsMeta = normalizedOperations.reduce((acc, op) => {
        const meta = (raw.operationsMeta && raw.operationsMeta[op]) || {};
        acc[String(op)] = {
            status: operationsStatus[String(op)] || 'pending',
            notes: meta.notes || '',
            updatedAt: meta.updatedAt || null,
            updatedBy: meta.updatedBy || null,
            startedAt: meta.startedAt || null,
            completedAt: meta.completedAt || null,
        };
        return acc;
    }, {});

    return {
        vendorId: String(raw.vendorId || '').trim(),
        // Keep legacy single operation for backward compatibility (first operation if any)
        operation: normalizedOperations[0] ? String(normalizedOperations[0]).trim() : '',
        // New multi-operations field
        operations: normalizedOperations,
        // New per-operation status fields
        operationsStatus,
        operationsMeta,
        description: String(raw.description || ''),
        deliveryDate: raw.deliveryDate ? (raw.deliveryDate instanceof Date ? raw.deliveryDate : new Date(raw.deliveryDate)) : null,
        priority: raw.priority || 'medium',
        status: raw.status || 'pending',
        paymentTerms: String(raw.paymentTerms ?? '30'),
        shippingAddress: String(raw.shippingAddress || ''),
        billingAddress: String(raw.billingAddress || ''),
        currency: raw.currency || 'INR',
        invoiceDate: raw.invoiceDate || null,
        dueDate: raw.dueDate || null,
        stateOfSupply: String(raw.stateOfSupply || ''),
        taxRate,
        discountAmount,
        items: safeItems,
        totalAmount: Number(subtotal.toFixed(2)),
        taxAmount: Number(taxAmount.toFixed(2)),
        finalAmount: Number(finalAmount.toFixed(2)),
        roundOffEnabled,
        roundOffValue,
        specifications: String(raw.specifications || ''),
        qualityRequirements: String(raw.qualityRequirements || ''),
        expectedDeliveryDays: Number(raw.expectedDeliveryDays || 0) || 0,
        warrantPeriod: String(raw.warrantPeriod || ''),
        terms: String(raw.terms || ''),
        poNumber: raw.poNumber || null,
    };
}

export function mapPOFromDoc(docSnap) {
    const d = docSnap.data() || {};
    return { id: docSnap.id, ...d };
}

async function logActivity(poId, action, details) {
	try {
		await addDoc(collection(db, ACTIVITY_COLLECTION), { poId, action, details: details || {}, timestamp: serverTimestamp() });
	} catch (_) {}
}

export async function getPOsOrdered() {
	const q = query(collection(db, PO_COLLECTION), orderBy('createdAt', 'desc'));
	const snap = await getDocs(q);
	return snap.docs.map(mapPOFromDoc);
}

export async function getPOById(id) {
	const snap = await getDoc(doc(db, PO_COLLECTION, id));
	if (!snap.exists()) return null;
	return mapPOFromDoc(snap);
}

export async function createPO(raw) {
	const data = normalizePOForSave(raw);
	const now = serverTimestamp();
	const ref = await addDoc(collection(db, PO_COLLECTION), { ...data, createdAt: now, updatedAt: now });
	await logActivity(ref.id, 'create', { vendorId: data.vendorId, amount: data.finalAmount });
	return { id: ref.id, ...data };
}

export async function updatePO(id, raw) {
	const data = normalizePOForSave(raw);
	await updateDoc(doc(db, PO_COLLECTION, id), { ...data, updatedAt: serverTimestamp() });
	await logActivity(id, 'update', { status: data.status });
}

export async function deletePO(id) {
	await deleteDoc(doc(db, PO_COLLECTION, id));
	await logActivity(id, 'delete');
}

export async function bulkUpdatePOs(ids, fields) {
	const batch = writeBatch(db);
	ids.forEach((id) => batch.update(doc(db, PO_COLLECTION, id), { ...fields, updatedAt: serverTimestamp() }));
	await batch.commit();
	for (const id of ids) logActivity(id, 'bulk_update', { fields });
}

export async function bulkDeletePOs(ids) {
	const batch = writeBatch(db);
	ids.forEach((id) => batch.delete(doc(db, PO_COLLECTION, id)));
	await batch.commit();
	for (const id of ids) logActivity(id, 'bulk_delete');
}
