import { collection, addDoc, getDocs, getDoc, updateDoc, deleteDoc, doc, serverTimestamp, query, orderBy, where, writeBatch } from 'firebase/firestore';
import { db } from '../firebase';

const VENDORS_COLLECTION = 'vendors';
const VENDOR_ACTIVITY_COLLECTION = 'vendorActivity';

// Normalize vendor data coming from UI before saving
export function normalizeVendorForSave(raw) {
	const numeric = (v) => (v === '' || v === null || v === undefined ? null : Number(v));
	return {
		name: String(raw.name || '').trim(),
		email: String(raw.email || '').trim().toLowerCase(),
		phone: String(raw.phone || '').trim(),
		address: String(raw.address || '').trim(),
		city: String(raw.city || '').trim(),
		state: String(raw.state || '').trim(),
		zipCode: String(raw.zipCode || '').trim(),
		country: String(raw.country || 'India').trim(),
		website: String(raw.website || '').trim(),
		contactPerson: String(raw.contactPerson || '').trim(),
		contactPhone: String(raw.contactPhone || '').trim(),
		contactEmail: String(raw.contactEmail || '').trim().toLowerCase(),
		category: String(raw.category || '').trim(),
		rating: numeric(raw.rating) ?? 0,
		gstNumber: String(raw.gstNumber || '').trim(),
		panNumber: String(raw.panNumber || '').trim(),
		bankDetails: {
			accountNumber: String(raw.bankDetails?.accountNumber || '').trim(),
			ifscCode: String(raw.bankDetails?.ifscCode || '').trim(),
			bankName: String(raw.bankDetails?.bankName || '').trim(),
			accountHolderName: String(raw.bankDetails?.accountHolderName || '').trim(),
		},
		services: Array.isArray(raw.services) ? raw.services.filter(Boolean) : [],
		status: raw.status || 'active',
		notes: String(raw.notes || '').trim(),
		establishedYear: numeric(raw.establishedYear),
		employeeCount: numeric(raw.employeeCount),
		annualRevenue: numeric(raw.annualRevenue),
		creditLimit: numeric(raw.creditLimit),
		preferredPaymentTerms: numeric(raw.preferredPaymentTerms) ?? 30,
		taxId: String(raw.taxId || '').trim(),
	};
}

// Safely map Firestore doc to UI shape with defaults
export function mapVendorFromDoc(docSnap) {
	const data = docSnap.data() || {};
	return {
		id: docSnap.id,
		name: data.name || '',
		email: data.email || '',
		phone: data.phone || '',
		address: data.address || '',
		city: data.city || '',
		state: data.state || '',
		zipCode: data.zipCode || '',
		country: data.country || 'India',
		website: data.website || '',
		contactPerson: data.contactPerson || '',
		contactPhone: data.contactPhone || '',
		contactEmail: data.contactEmail || '',
		category: data.category || '',
		rating: typeof data.rating === 'number' ? data.rating : Number(data.rating || 0),
		gstNumber: data.gstNumber || '',
		panNumber: data.panNumber || '',
		bankDetails: {
			accountNumber: data.bankDetails?.accountNumber || '',
			ifscCode: data.bankDetails?.ifscCode || '',
			bankName: data.bankDetails?.bankName || '',
			accountHolderName: data.bankDetails?.accountHolderName || '',
		},
		services: Array.isArray(data.services) ? data.services : [],
		status: data.status || 'active',
		notes: data.notes || '',
		establishedYear: data.establishedYear ?? '',
		employeeCount: data.employeeCount ?? '',
		annualRevenue: data.annualRevenue ?? '',
		creditLimit: data.creditLimit ?? '',
		preferredPaymentTerms: data.preferredPaymentTerms ?? 30,
		taxId: data.taxId || '',
		createdAt: data.createdAt,
		updatedAt: data.updatedAt,
	};
}

export async function getVendorsOrdered() {
	const q = query(collection(db, VENDORS_COLLECTION), orderBy('createdAt', 'desc'));
	const snapshot = await getDocs(q);
	return snapshot.docs.map(mapVendorFromDoc);
}

export async function getAllVendors() {
	const snapshot = await getDocs(collection(db, VENDORS_COLLECTION));
	return snapshot.docs.map(mapVendorFromDoc);
}

export async function getVendorById(id) {
	const snap = await getDoc(doc(db, VENDORS_COLLECTION, id));
	if (!snap.exists()) return null;
	return mapVendorFromDoc(snap);
}

async function findDuplicateVendor(email, phone, excludeId) {
	const dupCandidates = [];
	if (email) {
		const emailQ = query(collection(db, VENDORS_COLLECTION), where('email', '==', email));
		const emailSnap = await getDocs(emailQ);
		emailSnap.forEach((d) => dupCandidates.push(d));
	}
	if (phone) {
		const phoneQ = query(collection(db, VENDORS_COLLECTION), where('phone', '==', phone));
		const phoneSnap = await getDocs(phoneQ);
		phoneSnap.forEach((d) => dupCandidates.push(d));
	}

	for (const d of dupCandidates) {
		if (!excludeId || d.id !== excludeId) {
			const data = d.data();
			if (email && data.email === email && (!excludeId || d.id !== excludeId)) {
				return { type: 'email', id: d.id };
			}
			if (phone && data.phone === phone && (!excludeId || d.id !== excludeId)) {
				return { type: 'phone', id: d.id };
			}
		}
	}
	return null;
}

async function logVendorActivity(vendorId, action, details) {
	try {
		await addDoc(collection(db, VENDOR_ACTIVITY_COLLECTION), {
			vendorId,
			action,
			details: details || {},
			timestamp: serverTimestamp(),
		});
	} catch (_) {
		// Best effort logging; ignore failures
	}
}

export async function createVendor(raw) {
	const data = normalizeVendorForSave(raw);
	const duplicate = await findDuplicateVendor(data.email, data.phone);
	if (duplicate) {
		const err = new Error(duplicate.type === 'email' ? 'DUPLICATE_VENDOR_EMAIL' : 'DUPLICATE_VENDOR_PHONE');
		throw err;
	}
	const now = serverTimestamp();
	const ref = await addDoc(collection(db, VENDORS_COLLECTION), { ...data, createdAt: now, updatedAt: now });
	await logVendorActivity(ref.id, 'create', { name: data.name, email: data.email });
}

// Get vendors with WhatsApp auto-send enabled (simplified query)
export async function getVendorsWithWhatsAppAutoSend() {
	try {
		// Use a simpler query to avoid composite index issues
		const q = query(
			collection(db, VENDORS_COLLECTION),
			where('status', '==', 'active')
		);
		const snapshot = await getDocs(q);
		
		// Filter in memory to avoid complex Firestore queries
		return snapshot.docs
			.map(mapVendorFromDoc)
			.filter(vendor => 
				vendor.whatsappAutoSend === true && 
				vendor.phone && 
				vendor.phone.trim() !== ''
			);
	} catch (error) {
		console.error('Error fetching vendors with WhatsApp auto-send:', error);
		// Return empty array instead of throwing to prevent app crashes
		return [];
	}
}

// Update vendor WhatsApp settings
export async function updateVendorWhatsAppSettings(vendorId, settings) {
	try {
		const vendorRef = doc(db, VENDORS_COLLECTION, vendorId);
		await updateDoc(vendorRef, {
			whatsappAutoSend: settings.autoSend || false,
			whatsappTemplate: settings.template || 'default',
			whatsappEnabled: settings.enabled || false,
			updatedAt: serverTimestamp()
		});

		// Log activity
		await logVendorActivity(vendorId, 'updated', {
			changes: `WhatsApp settings updated: ${JSON.stringify(settings)}`
		});

		return { success: true };
	} catch (error) {
		console.error('Error updating vendor WhatsApp settings:', error);
		throw error;
	}
}

export async function updateVendor(id, raw) {
	const data = normalizeVendorForSave(raw);
	const duplicate = await findDuplicateVendor(data.email, data.phone, id);
	if (duplicate) {
		const err = new Error(duplicate.type === 'email' ? 'DUPLICATE_VENDOR_EMAIL' : 'DUPLICATE_VENDOR_PHONE');
		throw err;
	}
	await updateDoc(doc(db, VENDORS_COLLECTION, id), { ...data, updatedAt: serverTimestamp() });
	await logVendorActivity(id, 'update', { name: data.name, email: data.email });
}

export async function deleteVendor(id) {
	await deleteDoc(doc(db, VENDORS_COLLECTION, id));
	await logVendorActivity(id, 'delete');
}

export async function bulkUpdateVendors(ids, fields) {
	const batch = writeBatch(db);
	ids.forEach((id) => {
		batch.update(doc(db, VENDORS_COLLECTION, id), { ...fields, updatedAt: serverTimestamp() });
	});
	await batch.commit();
	for (const id of ids) {
		logVendorActivity(id, 'bulk_update', { fields });
	}
}

export async function bulkDeleteVendors(ids) {
	const batch = writeBatch(db);
	ids.forEach((id) => batch.delete(doc(db, VENDORS_COLLECTION, id)));
	await batch.commit();
	for (const id of ids) {
		logVendorActivity(id, 'bulk_delete');
	}
}

export async function queryPOsByVendorId(vendorId) {
	const q = query(collection(db, 'purchaseOrders'), where('vendorId', '==', vendorId));
	const snapshot = await getDocs(q);
	return snapshot.docs.map((d) => d.data());
}

export async function queryJobsByVendorId(vendorId) {
	const q = query(collection(db, 'jobAssignments'), where('vendorId', '==', vendorId));
	const snapshot = await getDocs(q);
	return snapshot.docs.map((d) => d.data());
}

export async function computeVendorStats(vendors) {
	const stats = {};
	for (const vendor of vendors) {
		try {
			const [pos, jobs] = await Promise.all([
				queryPOsByVendorId(vendor.id),
				queryJobsByVendorId(vendor.id),
			]);
			stats[vendor.id] = {
				totalPOs: pos.length,
				totalJobs: jobs.length,
				totalSpend: pos.reduce((sum, po) => sum + (parseFloat(po.totalAmount) || 0), 0),
				completedJobs: jobs.filter((job) => job.status === 'completed').length,
				pendingPOs: pos.filter((po) => po.status === 'pending').length,
				avgRating: vendor.rating || 0,
				lastActivity: Math.max(
					...pos.map((po) => po.createdAt?.toDate?.()?.getTime() || 0),
					...jobs.map((job) => job.createdAt?.toDate?.()?.getTime() || 0)
				),
			};
		} catch (e) {
			stats[vendor.id] = {
				totalPOs: 0,
				totalJobs: 0,
				totalSpend: 0,
				completedJobs: 0,
				pendingPOs: 0,
				avgRating: 0,
				lastActivity: 0,
			};
		}
	}
	return stats;
}
