// File upload service for sharing PDFs via WhatsApp
class FileUploadService {
  constructor() {
    // Using a simple file sharing service (you can replace with your preferred service)
    this.uploadEndpoint = 'https://file.io'; // Free file sharing service
  }

  // Upload PDF file and get shareable URL
  async uploadPDF(pdfBlob, filename) {
    try {
      const formData = new FormData();
      formData.append('file', pdfBlob, filename);

      const response = await fetch(this.uploadEndpoint, {
        method: 'POST',
        body: formData
      });

      if (!response.ok) {
        throw new Error(`Upload failed: ${response.statusText}`);
      }

      const result = await response.json();
      
      if (result.success) {
        return {
          success: true,
          url: result.link,
          filename: filename,
          expires: result.expires || '1 day'
        };
      } else {
        throw new Error('Upload failed: Invalid response');
      }
    } catch (error) {
      console.error('File upload error:', error);
      return {
        success: false,
        error: error.message
      };
    }
  }

  // Alternative: Use data URL for smaller files (base64 encoded)
  async createDataURL(pdfBlob) {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = () => resolve(reader.result);
      reader.readAsDataURL(pdfBlob);
    });
  }

  // Generate shareable link using data URL (for smaller files)
  async generateShareableLink(pdfBlob, filename) {
    try {
      // For small files, we can use data URLs directly
      const dataURL = await this.createDataURL(pdfBlob);
      
      // Create a simple sharing page (you can host this on your server)
      const shareData = {
        filename: filename,
        data: dataURL,
        timestamp: Date.now()
      };

      // Store in localStorage for demo purposes (in production, use a proper backend)
      const shareId = `po_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      localStorage.setItem(`share_${shareId}`, JSON.stringify(shareData));

      // Return a local URL (in production, this would be your server URL)
      const shareUrl = `${window.location.origin}/share/${shareId}`;
      
      return {
        success: true,
        url: shareUrl,
        filename: filename,
        expires: '7 days'
      };
    } catch (error) {
      console.error('Data URL generation error:', error);
      return {
        success: false,
        error: error.message
      };
    }
  }
}

// Create and export singleton instance
const fileUploadService = new FileUploadService();
export default fileUploadService;
