import { collection, addDoc, getDoc, getDocs, updateDoc, deleteDoc, doc, serverTimestamp, query, orderBy, where, writeBatch } from 'firebase/firestore';
import { getDownloadURL, ref, uploadBytes } from 'firebase/storage';
import { db } from '../firebase';
import { storage } from '../firebase';

const ITEMS_COLLECTION = 'items';

export function normalizeItemForSave(raw) {
  const numeric = (v) => (v === '' || v === null || v === undefined ? null : Number(v));
  return {
    name: String(raw.name || '').trim(),
    type: raw.type === 'service' ? 'service' : 'product',
    hsn: String(raw.hsn || '').trim(),
    code: String(raw.code || '').trim(),
    unit: String(raw.unit || '').trim(),
    category: String(raw.category || '').trim(),
    imageUrl: String(raw.imageUrl || '').trim(),
    pricing: {
      salePrice: numeric(raw.pricing?.salePrice) ?? 0,
      purchasePrice: numeric(raw.pricing?.purchasePrice) ?? 0,
      discountType: ['percentage', 'flat'].includes(raw.pricing?.discountType) ? raw.pricing.discountType : 'percentage',
      discountValue: numeric(raw.pricing?.discountValue) ?? 0,
      taxRate: numeric(raw.pricing?.taxRate) ?? 0,
      taxMode: ['with_tax', 'without_tax'].includes(raw.pricing?.taxMode) ? raw.pricing.taxMode : 'without_tax',
    },
    stock: {
      track: Boolean(raw.stock?.track),
      openingQty: numeric(raw.stock?.openingQty) ?? 0,
      minQty: numeric(raw.stock?.minQty) ?? 0,
      location: String(raw.stock?.location || '').trim(),
    },
    status: raw.status === 'inactive' ? 'inactive' : 'active',
    notes: String(raw.notes || '').trim(),
  };
}

export function mapItemFromDoc(docSnap) {
  const d = docSnap.data() || {};
  return {
    id: docSnap.id,
    name: d.name || '',
    type: d.type || 'product',
    hsn: d.hsn || '',
    code: d.code || '',
    unit: d.unit || '',
    category: d.category || '',
    imageUrl: d.imageUrl || '',
    pricing: {
      salePrice: typeof d.pricing?.salePrice === 'number' ? d.pricing.salePrice : Number(d.pricing?.salePrice || 0),
      purchasePrice: typeof d.pricing?.purchasePrice === 'number' ? d.pricing.purchasePrice : Number(d.pricing?.purchasePrice || 0),
      discountType: d.pricing?.discountType || 'percentage',
      discountValue: typeof d.pricing?.discountValue === 'number' ? d.pricing.discountValue : Number(d.pricing?.discountValue || 0),
      taxRate: typeof d.pricing?.taxRate === 'number' ? d.pricing.taxRate : Number(d.pricing?.taxRate || 0),
      taxMode: d.pricing?.taxMode || 'without_tax',
    },
    stock: {
      track: Boolean(d.stock?.track),
      openingQty: typeof d.stock?.openingQty === 'number' ? d.stock.openingQty : Number(d.stock?.openingQty || 0),
      minQty: typeof d.stock?.minQty === 'number' ? d.stock.minQty : Number(d.stock?.minQty || 0),
      location: d.stock?.location || '',
    },
    status: d.status || 'active',
    notes: d.notes || '',
    createdAt: d.createdAt,
    updatedAt: d.updatedAt,
  };
}

export function validateItem(raw) {
  const errors = {};
  if (!raw.name || String(raw.name).trim().length < 2) errors.name = 'Item name is required';
  if (!raw.unit) errors.unit = 'Unit is required';
  if (raw.pricing && raw.pricing.salePrice < 0) errors.salePrice = 'Sale price must be >= 0';
  if (raw.pricing && raw.pricing.purchasePrice < 0) errors.purchasePrice = 'Purchase price must be >= 0';
  if (raw.pricing && raw.pricing.taxRate < 0) errors.taxRate = 'Tax rate must be >= 0';
  return errors;
}

export async function uploadItemImage(file, itemIdHint) {
  const path = `items/${itemIdHint || 'tmp'}_${Date.now()}_${file.name}`;
  const storageRef = ref(storage, path);
  const snap = await uploadBytes(storageRef, file);
  return getDownloadURL(snap.ref);
}

export async function createItem(raw) {
  const errors = validateItem(raw);
  if (Object.keys(errors).length) {
    const err = new Error('ITEM_VALIDATION_FAILED');
    err.details = errors;
    throw err;
  }
  const data = normalizeItemForSave(raw);
  const now = serverTimestamp();
  const ref = await addDoc(collection(db, ITEMS_COLLECTION), { ...data, createdAt: now, updatedAt: now });
  return { id: ref.id, ...data };
}

export async function updateItem(id, raw) {
  const errors = validateItem(raw);
  if (Object.keys(errors).length) {
    const err = new Error('ITEM_VALIDATION_FAILED');
    err.details = errors;
    throw err;
  }
  const data = normalizeItemForSave(raw);
  await updateDoc(doc(db, ITEMS_COLLECTION, id), { ...data, updatedAt: serverTimestamp() });
}

export async function deleteItem(id) {
  await deleteDoc(doc(db, ITEMS_COLLECTION, id));
}

export async function getItemById(id) {
  const snap = await getDoc(doc(db, ITEMS_COLLECTION, id));
  if (!snap.exists()) return null;
  return mapItemFromDoc(snap);
}

export async function getItemsOrdered() {
  const q = query(collection(db, ITEMS_COLLECTION), orderBy('createdAt', 'desc'));
  const snapshot = await getDocs(q);
  return snapshot.docs.map(mapItemFromDoc);
}

export async function searchItems({ text, type, category, status }) {
  // Basic fetch and in-memory filter to avoid complex composite indexes
  const snapshot = await getDocs(collection(db, ITEMS_COLLECTION));
  let items = snapshot.docs.map(mapItemFromDoc);
  if (text) {
    const t = text.toLowerCase();
    items = items.filter((i) => (i.name || '').toLowerCase().includes(t) || (i.code || '').toLowerCase().includes(t) || (i.hsn || '').toLowerCase().includes(t));
  }
  if (type) items = items.filter((i) => i.type === type);
  if (category) items = items.filter((i) => i.category === category);
  if (status) items = items.filter((i) => i.status === status);
  return items;
}

export async function bulkDeleteItems(ids) {
  const batch = writeBatch(db);
  ids.forEach((id) => batch.delete(doc(db, ITEMS_COLLECTION, id)));
  await batch.commit();
}


