// WhatsApp Business API integration service
// This service handles sending PO details to vendors via WhatsApp

import pdfService from './pdfService';
import fileUploadService from './fileUploadService';
import messageTemplateService from './messageTemplateService';

class WhatsAppService {
  constructor() {
    // WhatsApp Business API configuration
    this.baseURL = process.env.REACT_APP_WHATSAPP_API_URL || 'https://graph.facebook.com/v18.0';
    this.accessToken = process.env.REACT_APP_WHATSAPP_ACCESS_TOKEN || '1306260161141866|7yXEM5nBjGO291Bz@NXLEvzJFto';
    this.phoneNumberId = process.env.REACT_APP_WHATSAPP_PHONE_NUMBER_ID || '9503367989';
    this.businessName = process.env.REACT_APP_BUSINESS_NAME || 'VMT Admin';
  }

  // Format phone number to international format
  formatPhoneNumber(phone) {
    // Remove all non-digit characters
    let cleaned = phone.replace(/\D/g, '');
    
    // Add country code if not present (assuming India +91)
    if (cleaned.length === 10) {
      cleaned = '91' + cleaned;
    } else if (cleaned.startsWith('0')) {
      cleaned = '91' + cleaned.substring(1);
    }
    
    return cleaned;
  }

  // Send PO details to vendor via WhatsApp (PDF version)
  async sendPOToVendor(poData, vendorData) {
    try {
      // Generate PDF
      const pdf = await pdfService.generatePOPDF(poData, vendorData);
      const vendorPhone = this.formatPhoneNumber(vendorData.phone);
      
      // Generate filename
      const filename = `PO_${poData.poNumber || Date.now()}_${vendorData.name.replace(/\s+/g, '_')}.pdf`;
      
      // Get PDF as blob
      const pdfBlob = pdfService.getPDFBlob(pdf);
      
      // Try Web Share API first (for mobile devices)
      if (navigator.share && navigator.canShare) {
        try {
          const file = new File([pdfBlob], filename, { type: 'application/pdf' });
          
          if (navigator.canShare({ files: [file] })) {
            await navigator.share({
              title: `Purchase Order #${poData.poNumber || 'N/A'}`,
              text: `Hello ${vendorData.name},\n\nPlease find the Purchase Order #${poData.poNumber || 'N/A'} attached.\n\nThank you!`,
              files: [file]
            });
            
            return {
              success: true,
              messageId: 'pdf_shared_native',
              phone: vendorPhone,
              filename: filename,
              message: `PDF shared directly via device sharing.`
            };
          }
        } catch (shareError) {
          console.log('Web Share API failed, falling back to other methods:', shareError);
        }
      }
      
      // Try to upload PDF and get shareable URL
      const uploadResult = await fileUploadService.uploadPDF(pdfBlob, filename);
      
      if (uploadResult.success) {
      // Use uploaded PDF URL
      const message = messageTemplateService.generatePOMessageWithPDF(poData, vendorData, uploadResult.url);
      const whatsappUrl = `https://wa.me/${vendorPhone}?text=${encodeURIComponent(message)}`;
      window.open(whatsappUrl, '_blank');
        
        return {
          success: true,
          messageId: 'pdf_uploaded',
          phone: vendorPhone,
          whatsappUrl: whatsappUrl,
          filename: filename,
          shareUrl: uploadResult.url,
          message: `PDF uploaded and shared via link. WhatsApp Web opened with the link.`
        };
      }
      
      // Fallback: Download PDF and open WhatsApp with instructions
      pdfService.downloadPDF(pdf, filename);
      const message = messageTemplateService.generateSimplePOMessage(poData, vendorData);
      const whatsappUrl = `https://wa.me/${vendorPhone}?text=${encodeURIComponent(message)}`;
      window.open(whatsappUrl, '_blank');
      
      return {
        success: true,
        messageId: 'pdf_downloaded',
        phone: vendorPhone,
        whatsappUrl: whatsappUrl,
        filename: filename,
        message: `PDF downloaded as "${filename}". WhatsApp Web opened. Please attach the downloaded PDF file.`
      };
      
    } catch (error) {
      console.error('WhatsApp PDF Service Error:', error);
      return { 
        success: false, 
        error: error.message || 'Failed to generate and share PDF for WhatsApp' 
      };
    }
  }

  // Generate formatted PO message for WhatsApp
  generatePOMessage(poData, vendorData) {
    return messageTemplateService.generatePOMessage(poData, vendorData);
  }

  // Send PO status update to vendor
  async sendPOStatusUpdate(poData, vendorData, newStatus, oldStatus) {
    try {
      if (!this.accessToken || !this.phoneNumberId) {
        return { success: false, error: 'WhatsApp API not configured' };
      }

      const vendorPhone = this.formatPhoneNumber(vendorData.phone);
      const message = messageTemplateService.generateStatusUpdateMessage(poData, newStatus, oldStatus);

      const response = await fetch(`${this.baseURL}/${this.phoneNumberId}/messages`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.accessToken}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          messaging_product: 'whatsapp',
          to: vendorPhone,
          type: 'text',
          text: {
            body: message
          }
        })
      });

      const result = await response.json();
      return response.ok ? { success: true, messageId: result.messages[0].id } : { success: false, error: result.error?.message };
    } catch (error) {
      console.error('WhatsApp Status Update Error:', error);
      return { success: false, error: error.message };
    }
  }


  // Send custom message to vendor
  async sendCustomMessage(vendorPhone, message) {
    try {
      if (!this.accessToken || !this.phoneNumberId) {
        return { success: false, error: 'WhatsApp API not configured' };
      }

      const formattedPhone = this.formatPhoneNumber(vendorPhone);

      const response = await fetch(`${this.baseURL}/${this.phoneNumberId}/messages`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.accessToken}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          messaging_product: 'whatsapp',
          to: formattedPhone,
          type: 'text',
          text: {
            body: message
          }
        })
      });

      const result = await response.json();
      return response.ok ? { success: true, messageId: result.messages[0].id } : { success: false, error: result.error?.message };
    } catch (error) {
      console.error('WhatsApp Custom Message Error:', error);
      return { success: false, error: error.message };
    }
  }

  // Send document/file to vendor (for PO PDF)
  async sendDocument(vendorPhone, documentUrl, filename, caption) {
    try {
      if (!this.accessToken || !this.phoneNumberId) {
        return { success: false, error: 'WhatsApp API not configured' };
      }

      const formattedPhone = this.formatPhoneNumber(vendorPhone);

      const response = await fetch(`${this.baseURL}/${this.phoneNumberId}/messages`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.accessToken}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          messaging_product: 'whatsapp',
          to: formattedPhone,
          type: 'document',
          document: {
            link: documentUrl,
            filename: filename,
            caption: caption
          }
        })
      });

      const result = await response.json();
      return response.ok ? { success: true, messageId: result.messages[0].id } : { success: false, error: result.error?.message };
    } catch (error) {
      console.error('WhatsApp Document Send Error:', error);
      return { success: false, error: error.message };
    }
  }

  // Validate WhatsApp configuration (simplified - always works)
  isConfigured() {
    return true; // Simplified version always works via WhatsApp Web
  }

  // Test WhatsApp connection
  async testConnection() {
    try {
      if (!this.isConfigured()) {
        return { success: false, error: 'WhatsApp API not configured' };
      }

      const response = await fetch(`${this.baseURL}/${this.phoneNumberId}`, {
        headers: {
          'Authorization': `Bearer ${this.accessToken}`,
        }
      });

      return response.ok ? { success: true } : { success: false, error: 'Invalid credentials' };
    } catch (error) {
      return { success: false, error: error.message };
    }
  }
}

// Create and export singleton instance
const whatsappService = new WhatsAppService();
export default whatsappService;
