// Message template service for WhatsApp communications
class MessageTemplateService {
  constructor() {
    this.businessName = process.env.REACT_APP_BUSINESS_NAME || 'VMT Admin';
    this.websiteUrl = 'https://vmttech.in';
    this.companyName = 'VMT Technologies';
  }

  // Generate PO message template with link
  generatePOMessage(poData, vendorData) {
    const poNumber = poData.poNumber || 'N/A';
    const totalAmount = parseFloat(poData.finalAmount || poData.totalAmount || 0).toLocaleString();
    const deliveryDate = poData.deliveryDate ? new Date(poData.deliveryDate).toLocaleDateString() : 'TBD';
    
    return `🏢 *${this.companyName}* - Purchase Order

Dear ${vendorData.name},

We are pleased to share with you our Purchase Order details:

📋 *PO Details:*
• PO Number: *${poNumber}*
• Date: ${new Date().toLocaleDateString()}
• Total Amount: *₹${totalAmount}*
• Delivery Date: ${deliveryDate}

${poData.operation ? `• Operation: ${poData.operation}` : ''}

📄 *Document:*
Please find the complete Purchase Order document attached with all terms, conditions, and item details.

🌐 *Our Website:*
Visit us at: ${this.websiteUrl}
For more information about our services and company profile.

📞 *Contact Information:*
For any queries or clarifications, please contact us directly.

Thank you for your continued partnership!

Best regards,
${this.businessName}
${this.websiteUrl}`;
  }

  // Generate PO message template for PDF sharing
  generatePOMessageWithPDF(poData, vendorData, pdfUrl = null) {
    const poNumber = poData.poNumber || 'N/A';
    const totalAmount = parseFloat(poData.finalAmount || poData.totalAmount || 0).toLocaleString();
    const deliveryDate = poData.deliveryDate ? new Date(poData.deliveryDate).toLocaleDateString() : 'TBD';
    
    let message = `🏢 *${this.companyName}* - Purchase Order

Dear ${vendorData.name},

We are pleased to share with you our Purchase Order details:

📋 *PO Details:*
• PO Number: *${poNumber}*
• Date: ${new Date().toLocaleDateString()}
• Total Amount: *₹${totalAmount}*
• Delivery Date: ${deliveryDate}

${poData.operation ? `• Operation: ${poData.operation}` : ''}`;

    if (pdfUrl) {
      message += `\n\n📄 *Document Link:*
${pdfUrl}

Please download the complete Purchase Order document with all terms, conditions, and item details.`;
    } else {
      message += `\n\n📄 *Document:*
Please find the complete Purchase Order document attached with all terms, conditions, and item details.`;
    }

    message += `\n\n🌐 *Our Website:*
Visit us at: ${this.websiteUrl}
For more information about our services and company profile.

📞 *Contact Information:*
For any queries or clarifications, please contact us directly.

Thank you for your continued partnership!

Best regards,
${this.businessName}
${this.websiteUrl}`;

    return message;
  }

  // Generate simple PO message for direct file sharing
  generateSimplePOMessage(poData, vendorData) {
    const poNumber = poData.poNumber || 'N/A';
    
    return `Hello ${vendorData.name},

Please find attached the Purchase Order #${poNumber}.

Visit us: ${this.websiteUrl}

Thank you!
${this.businessName}`;
  }

  // Generate status update message template
  generateStatusUpdateMessage(poData, newStatus, oldStatus) {
    const poNumber = poData.poNumber || 'N/A';
    const statusEmojis = {
      pending: '⏳',
      approved: '✅',
      rejected: '❌',
      completed: '🎉',
      in_progress: '🔄',
      cancelled: '❌'
    };

    const statusMessages = {
      approved: 'Your Purchase Order has been approved! You can proceed with the work.',
      rejected: 'Your Purchase Order has been rejected. Please contact us for more details.',
      completed: 'Purchase Order has been marked as completed. Thank you for your service!',
      in_progress: 'Purchase Order is now in progress. Work has begun.',
      cancelled: 'Purchase Order has been cancelled. Please contact us for more details.'
    };

    return `🏢 *${this.companyName}* - PO Status Update

PO Number: *${poNumber}*
Status: ${oldStatus} → *${newStatus.toUpperCase()}*

${statusEmojis[newStatus]} ${statusMessages[newStatus] || `PO status has been updated to ${newStatus}.`}

🌐 Visit us: ${this.websiteUrl}

${this.businessName}`;
  }

  // Generate general message template
  generateGeneralMessage(subject, content, vendorData) {
    return `🏢 *${this.companyName}* - ${subject}

Dear ${vendorData.name},

${content}

🌐 Visit us: ${this.websiteUrl}

Best regards,
${this.businessName}`;
  }

  // Generate welcome message for new vendors
  generateWelcomeMessage(vendorData) {
    return `🏢 *${this.companyName}* - Welcome!

Dear ${vendorData.name},

Welcome to our vendor network! We're excited to work with you.

🌐 *Our Website:*
Visit us at: ${this.websiteUrl}
Learn more about our services and company profile.

📞 *Contact Information:*
For any questions or support, please don't hesitate to contact us.

We look forward to a successful partnership!

Best regards,
${this.businessName}
${this.websiteUrl}`;
  }
}

// Create and export singleton instance
const messageTemplateService = new MessageTemplateService();
export default messageTemplateService;
