import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

class PDFService {
  // Generate PDF from PO data
  async generatePOPDF(poData, vendorData) {
    try {
      // Create a temporary element to render the PO
      const tempElement = this.createPOElement(poData, vendorData);
      document.body.appendChild(tempElement);

      // Generate canvas from the element
      const canvas = await html2canvas(tempElement, {
        scale: 2,
        useCORS: true,
        allowTaint: true,
        backgroundColor: '#ffffff'
      });

      // Remove temporary element
      document.body.removeChild(tempElement);

      // Create PDF
      const imgData = canvas.toDataURL('image/png');
      const pdf = new jsPDF('p', 'mm', 'a4');
      
      const imgWidth = 210; // A4 width in mm
      const pageHeight = 295; // A4 height in mm
      const imgHeight = (canvas.height * imgWidth) / canvas.width;
      let heightLeft = imgHeight;

      let position = 0;

      // Add first page
      pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
      heightLeft -= pageHeight;

      // Add additional pages if needed
      while (heightLeft >= 0) {
        position = heightLeft - imgHeight;
        pdf.addPage();
        pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;
      }

      return pdf;
    } catch (error) {
      console.error('Error generating PDF:', error);
      throw error;
    }
  }

  // Create temporary PO element for PDF generation
  createPOElement(poData, vendorData) {
    const element = document.createElement('div');
    element.style.position = 'absolute';
    element.style.left = '-9999px';
    element.style.top = '0';
    element.style.width = '210mm';
    element.style.backgroundColor = 'white';
    element.style.padding = '20px';
    element.style.fontFamily = 'Arial, sans-serif';
    element.style.fontSize = '12px';
    element.style.lineHeight = '1.4';

    element.innerHTML = `
      <div style="text-align: center; margin-bottom: 30px; border-bottom: 2px solid #333; padding-bottom: 20px;">
        <h1 style="font-size: 24px; margin: 0; color: #333;">PURCHASE ORDER</h1>
        <p style="font-size: 16px; margin: 5px 0; color: #666;">PO Number: ${poData.poNumber || 'PO' + Date.now()}</p>
        <p style="font-size: 14px; margin: 0; color: #666;">Date: ${new Date().toLocaleDateString()}</p>
      </div>

      <div style="display: flex; justify-content: space-between; margin-bottom: 30px;">
        <div style="width: 48%;">
          <h3 style="font-size: 14px; margin: 0 0 10px 0; color: #333;">From:</h3>
          <div style="font-size: 12px; line-height: 1.5;">
            <p style="margin: 2px 0; font-weight: bold;">Your Company Name</p>
            <p style="margin: 2px 0;">123 Business Street</p>
            <p style="margin: 2px 0;">City, State 12345</p>
            <p style="margin: 2px 0;">Phone: (555) 123-4567</p>
            <p style="margin: 2px 0;">Email: info@company.com</p>
          </div>
        </div>
        <div style="width: 48%;">
          <h3 style="font-size: 14px; margin: 0 0 10px 0; color: #333;">To:</h3>
          <div style="font-size: 12px; line-height: 1.5;">
            <p style="margin: 2px 0; font-weight: bold;">${vendorData.name}</p>
            <p style="margin: 2px 0;">${vendorData.address || 'N/A'}</p>
            <p style="margin: 2px 0;">Phone: ${vendorData.phone}</p>
            <p style="margin: 2px 0;">Email: ${vendorData.email || 'N/A'}</p>
          </div>
        </div>
      </div>

      <div style="margin-bottom: 20px; padding: 15px; background-color: #f8f9fa; border-radius: 5px;">
        <div style="display: grid; grid-template-columns: 1fr 1fr 1fr 1fr; gap: 15px; font-size: 12px;">
          <div>
            <strong>Operation:</strong><br>
            ${poData.operation || 'N/A'}
          </div>
          <div>
            <strong>Delivery Date:</strong><br>
            ${poData.deliveryDate ? new Date(poData.deliveryDate).toLocaleDateString() : 'TBD'}
          </div>
          <div>
            <strong>Payment Terms:</strong><br>
            ${poData.paymentTerms || 30} Days
          </div>
          <div>
            <strong>Priority:</strong><br>
            ${poData.priority || 'Medium'}
          </div>
        </div>
      </div>

      <h3 style="font-size: 16px; margin: 20px 0 10px 0; color: #333;">Items / Services</h3>
      <table style="width: 100%; border-collapse: collapse; margin-bottom: 20px; font-size: 11px;">
        <thead>
          <tr style="background-color: #f8f9fa;">
            <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">Description</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: center;">Qty</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: center;">Unit</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: right;">Unit Price</th>
            <th style="border: 1px solid #ddd; padding: 8px; text-align: right;">Amount</th>
          </tr>
        </thead>
        <tbody>
          ${(poData.items || []).map(item => `
            <tr>
              <td style="border: 1px solid #ddd; padding: 8px;">
                <div style="font-weight: bold;">${item.description || 'N/A'}</div>
                ${item.specifications ? `<div style="font-size: 10px; color: #666; margin-top: 2px;">${item.specifications}</div>` : ''}
              </td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${item.quantity || 0}</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: center;">${item.unit || 'pcs'}</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">₹${parseFloat(item.unitPrice || 0).toLocaleString()}</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">₹${parseFloat(item.amount || 0).toLocaleString()}</td>
            </tr>
          `).join('')}
        </tbody>
        <tfoot>
          <tr style="background-color: #f8f9fa; font-weight: bold;">
            <td colspan="4" style="border: 1px solid #ddd; padding: 8px; text-align: right;">Subtotal:</td>
            <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">₹${parseFloat(poData.totalAmount || 0).toLocaleString()}</td>
          </tr>
          ${parseFloat(poData.discountAmount || 0) > 0 ? `
            <tr style="background-color: #f8f9fa; font-weight: bold;">
              <td colspan="4" style="border: 1px solid #ddd; padding: 8px; text-align: right;">Discount:</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: right; color: red;">-₹${parseFloat(poData.discountAmount || 0).toLocaleString()}</td>
            </tr>
          ` : ''}
          ${parseFloat(poData.taxAmount || 0) > 0 ? `
            <tr style="background-color: #f8f9fa; font-weight: bold;">
              <td colspan="4" style="border: 1px solid #ddd; padding: 8px; text-align: right;">Tax (${poData.taxRate || 0}%):</td>
              <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">₹${parseFloat(poData.taxAmount || 0).toLocaleString()}</td>
            </tr>
          ` : ''}
          <tr style="background-color: #e9ecef; font-weight: bold; font-size: 12px;">
            <td colspan="4" style="border: 1px solid #ddd; padding: 10px; text-align: right;">Total Amount:</td>
            <td style="border: 1px solid #ddd; padding: 10px; text-align: right;">₹${parseFloat(poData.finalAmount || 0).toLocaleString()}</td>
          </tr>
        </tfoot>
      </table>

      ${poData.description ? `
        <div style="margin-bottom: 20px;">
          <h4 style="font-size: 14px; margin: 0 0 8px 0; color: #333;">Description:</h4>
          <p style="font-size: 11px; line-height: 1.4; margin: 0;">${poData.description}</p>
        </div>
      ` : ''}

      ${poData.specifications ? `
        <div style="margin-bottom: 20px;">
          <h4 style="font-size: 14px; margin: 0 0 8px 0; color: #333;">Technical Specifications:</h4>
          <p style="font-size: 11px; line-height: 1.4; margin: 0; white-space: pre-line;">${poData.specifications}</p>
        </div>
      ` : ''}

      ${poData.terms ? `
        <div style="margin-bottom: 20px;">
          <h4 style="font-size: 14px; margin: 0 0 8px 0; color: #333;">Terms & Conditions:</h4>
          <p style="font-size: 11px; line-height: 1.4; margin: 0; white-space: pre-line;">${poData.terms}</p>
        </div>
      ` : ''}

      <div style="margin-top: 40px; display: flex; justify-content: space-between;">
        <div style="width: 45%;">
          <div style="border-top: 1px solid #333; padding-top: 5px; text-align: center;">
            <p style="font-size: 11px; margin: 0; color: #666;">Authorized Signature</p>
            <p style="font-size: 10px; margin: 5px 0 0 0; color: #666;">Name & Title</p>
            <p style="font-size: 10px; margin: 0; color: #666;">Date: ${new Date().toLocaleDateString()}</p>
          </div>
        </div>
        <div style="width: 45%;">
          <div style="border-top: 1px solid #333; padding-top: 5px; text-align: center;">
            <p style="font-size: 11px; margin: 0; color: #666;">Vendor Acknowledgment</p>
            <p style="font-size: 10px; margin: 5px 0 0 0; color: #666;">Authorized Signature</p>
            <p style="font-size: 10px; margin: 0; color: #666;">Date: ___________________</p>
          </div>
        </div>
      </div>
    `;

    return element;
  }

  // Download PDF
  downloadPDF(pdf, filename) {
    pdf.save(filename || `PO_${Date.now()}.pdf`);
  }

  // Get PDF as blob for WhatsApp sending
  getPDFBlob(pdf) {
    return pdf.output('blob');
  }

  // Get PDF as data URL for WhatsApp sending
  getPDFDataURL(pdf) {
    return pdf.output('datauristring');
  }
}

// Create and export singleton instance
const pdfService = new PDFService();
export default pdfService;
